import { Component, ViewChild  } from '@angular/core';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { MatTable } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { DialogBoxComponent } from './dialog-box/dialog-box.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class AppComponent {
  title = 'pizza-task';
  dataSource = ELEMENT_DATA;
  columnsToDisplay = ['position','name', 'items', 'amount', 'status', 'action'];
  expandedElement: PeriodicElement | null;

  @ViewChild(MatTable,{static:true}) table: MatTable<any>;

  constructor(public dialog: MatDialog) {}

  changeStatus(e: any, row: any): void {
    e.stopPropagation();
    const index = this.dataSource.indexOf(row, 0);
    if (index > -1) {
      let curStatus = this.dataSource[index].status
      if(curStatus == 'Order Received')
        this.dataSource[index].status = 'Preparing'
      else if(curStatus == 'Preparing')
        this.dataSource[index].status = 'Ready to Serve'
    }
    this.table.renderRows();
  }

  openDialog(obj) {;
    const dialogRef = this.dialog.open(DialogBoxComponent, {
      disableClose: true,
      width: 'auto',
      data:obj
    });
  }
}

export interface PeriodicElement {
  name: string;
  position: number;
  items: number;
  amount: number;
  status: string;
  address: string;
  list: Product[];
}

type Product = { price: number; dish: string; quantity: number };

